<template>
  <div class="container">
    <SecondTitle :title="secondTitle" @cancel="back"></SecondTitle>
    <div class="filter">
      <div class="filterNet">
        <p class="filterName">滤网</p>
        <Circular :circularDataList="filterNetData"></Circular>
        <p class="tip">建议定期清理滤网，保证设备正常工作</p>
      </div>
      <div class="division"></div>
      <div class="filterCore">
        <p class="filterName">滤芯</p>
        <Circular :circularDataList="filterCoreData"></Circular>
      </div>
      <div class="resetButton">
        <p class="tip" :style="{opacity: isChecked ? 1 : 0.5}"><b @click.stop="changeChecked" :class="['checkbox-icon', {'isChecked': isChecked}]"></b>请确定您已更换滤芯，点击以下按钮将重新开始统计</p>
        <div :class="['button', {'isChecked': isChecked}]" @click="showConfirm">复位滤芯</div>
      </div>
    </div>
    <Confirm v-if="isConfirmShow" :confirmList="confirmList" @cancel="confirmCancel" @submit="confirmSubmit">
      {{confirmText}}
    </Confirm>
  </div>
</template>
<script>
import {mapState} from 'vuex'
import SecondTitle from './SecondTitle'
import Circular from './Circular'
import Confirm from './Confirm'
export default {
  components: {
    SecondTitle,
    Circular,
    Confirm
  },
  mounted () {
    // if (this.cleanerRobot.filternet === 0) {
    //   this.isConfirmShow = 1
    //   if (this.cleanerRobot.filtercore === 0) {
    //     this.confirmText = this.confirmTextList[2]
    //   } else {
    //     this.confirmText = this.confirmTextList[0]
    //   }
    // } else if (this.cleanerRobot.filtercore === 0) {
    //   this.isConfirmShow = 1
    //   this.confirmText = this.confirmTextList[1]
    // }
  },
  data () {
    return {
      secondTitle: '滤芯滤网', // 二级标题
      isChecked: 0, // 是否勾选
      isConfirmShow: 0, // 确认框是否显示
      confirmList: {
        submitButton: '查看详情',
        cancelButton: '忽略'
      },
      confirmTextList: [
        '设备名称的滤网需清洗。',
        '设备名称的滤芯需更换。',
        '设备名称的滤芯需更换且滤网需清洗。',
        '您已更换滤芯，即将清空原有结果，重新开始统计滤芯消耗量。是否确定复位滤芯？'
      ],
      confirmText: ''
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    changeChecked () {
      this.isChecked = !this.isChecked
    },
    showConfirm () {
      if (!this.isChecked) return
      this.confirmList = {}
      this.confirmText = this.confirmTextList[3]
      this.isConfirmShow = 1
    },
    confirmCancel () {
      this.isConfirmShow = 0
      this.isChecked = 0
    },
    confirmSubmit () {
      this.isConfirmShow = 0
      if (this.isChecked) {
        this.isChecked = 0
        this.$store.dispatch('resetFiltercore')
      } else {
        alert('跳转滤网滤芯清洗教程页面，暂缺')
      }
    }
  },
  computed: {
    ...mapState(['cleanerRobot']),
    filterNetData () {
      return {
        currentPercent: this.cleanerRobot.filternet,
        index: 0
      }
    },
    filterCoreData () {
      return {
        currentPercent: this.cleanerRobot.filtercore,
        index: 1
      }
    }
  }
}
</script>
<style scoped>
  .container{
    width: 100%;
    height: 100%;
    background: #ffffff;
    display: flex;
    flex-direction: column;
  }
  .container::before{
    display: table;
    content: '';
  }
  .filter{
    flex: 1;
    display: flex;
    padding-bottom: 2rem;
    flex-direction: column;
  }
  .filterNet, .filterCore{
    padding: 1.6rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }
  .filterCore{
    padding-bottom: 0;
  }
  .resetButton{
    text-align: center;
  }
  .division{
    height: 1rem;
    background: #F4F4F4;
  }
  .filterName{
    text-align: left;
    opacity: 0.8;
    font-size: 1.5rem;
    padding: 0 0 1.2rem 0.8rem;
  }
  .tip{
    height: 1.8rem;
    line-height: 1.8rem;
    font-size: 1.3rem;
    padding: 1rem 0 0 0;
    color: #000000;
    opacity: 0.5;
  }
  .checkbox-icon {
    display: inline-block;
    margin-right: 0.6rem;
    width: 1.8rem;
    height: 1.8rem;
    background-image: url(../../static/img/icon_box.png);
    background-repeat: no-repeat;
    -webkit-background-size: contain;
    background-size: contain;
    background-position: center;
    vertical-align: bottom;
    transition: all .3s;
  }
  .checkbox-icon.isChecked {
    background-image: url(../../static/img/icon_selected.png);
  }
  .button{
    width: 85%;
    font-size: 1.5rem;
    margin: 1.2rem auto 0;
    color:rgba(0,126,255,.2);
    line-height: 3.3rem;
    border-radius: 1.5rem;
    border: 1px solid rgba(0,0,0,.1);
    transition: all .3s;
  }
  .button.isChecked{
    color:#007dff;
  }
</style>



