<template>
  <div class="circular">
    <div class="canvas">
      <canvas id="canvas" ref="canvas" style="width: 16rem; height: 16rem"></canvas>
      <div class="percent">
        <div>
          <span class="number">0</span>
          <span style="font-size: 1.3rem;">%</span>
        </div>
        <div class="word">剩余</div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'circular',
  props: ['circularDataList'],
  data () {
    return {
      timer: '',
      defaultPercent: 0,
      width: this.circularDataList.size ? this.circularDataList.size.width : 300, // 圆环宽度
      height: this.circularDataList.size ? this.circularDataList.size.height : 300, // 圆环高度
      radius: this.circularDataList.radius || 130, // 半径
      lineWidth: this.circularDataList.lineWidth || 20, // 圆环宽度
      lineCap: 'round', // 圆角
      circleAngle: 2.25 * Math.PI, // 结束弧度
      startAngle: 0.75 * Math.PI, // 开始弧度
      percent: this.circularDataList.currentPercent || 0, // 百分比
      strokeStyle: this.circularDataList.strokeStyle || 'rgba(0, 0, 0, .06)', // 背景圆环颜色
      fillColor: this.circularDataList.fillColor || {'low': ['#d4543d', '#f78a4d'], 'middle': ['#f5c44a', '#e3773a'], 'high': ['#52a1f7', '#6885f6']} // 颜色列表
    }
  },
  mounted () {
    this.draw()
  },
  computed: {
    // 更新百分比变化
    currentPercent () {
      return this.circularDataList.currentPercent
    },
    // 根据百分比改变级别
    level () {
      if (this.percent <= 10) {
        return 'low'
      } else if (this.percent > 60) {
        return 'high'
      } else {
        return 'middle'
      }
    }
  },
  updated () {
    this.draw()
  },
  methods: {
    toLink () {
      this.$emit('toLink')
    },
    // 绘制进度环
    draw () {
      let canvas = this.$refs.canvas
      let ctx = canvas.getContext('2d')
      canvas.width = this.width * 3
      canvas.height = this.height * 3
      ctx.clearRect(0, 0, canvas.width, canvas.height)
      this.circle(ctx)
      if (this.percent !== 0) {
        this.timer = setInterval(() => {
          this.ring(ctx)
        }, 10)
      }
    },
    // 绘制进度环背景
    circle (ctx) {
      ctx.beginPath()
      ctx.arc(this.width * 3 / 2, this.height * 3 / 2, this.radius * 3, this.startAngle, this.circleAngle, false)
      ctx.lineWidth = this.lineWidth * 3
      ctx.strokeStyle = this.strokeStyle
      ctx.lineCap = this.lineCap
      ctx.stroke() // 这里用stroke画一个空心圆背景;
    },
    // 绘制进度环内部进度条
    ring (ctx) {
      let element = document.getElementsByClassName('number')[this.circularDataList.index]
      if (this.defaultPercent >= this.percent) {
        this.defaultPercent = this.percent
        clearInterval(this.timer)
      } else {
        this.defaultPercent += 1
      }
      // 圆环内部文字百分比变化
      element.innerText = this.defaultPercent
      ctx.beginPath()
      let anglePerSec = this.defaultPercent / 100 * (this.circleAngle - this.startAngle)
      ctx.arc(this.width * 3 / 2, this.height * 3 / 2, this.radius * 3, this.startAngle, this.startAngle + anglePerSec, false) // 这里的圆心坐标要和cirle的保持一致
      ctx.strokeStyle = this.circularDataList.fillColor || this.drawLinearGradient(ctx)
      ctx.lineCap = this.lineCap
      ctx.stroke()
      ctx.closePath()
    },
    // 线性渐变绘制
    drawLinearGradient (ctx) {
      let gr
      if (this.percent < 10) {
        gr = ctx.createLinearGradient(0, this.height / 3 * 2, 0, 0)
      } else if (this.percent < 60 && this.percent > 10) {
        gr = ctx.createLinearGradient(0, 0, 0, this.height)
      } else {
        gr = ctx.createLinearGradient(this.width, 0, 0, 0)
      }
      // 添加颜色端点
      gr.addColorStop(0, this.fillColor[this.level][0])
      gr.addColorStop(1, this.fillColor[this.level][1])
      return gr
    }
  },
  watch: {
    currentPercent (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.percent = this.circularDataList.currentPercent
        this.draw()
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.circular{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.canvas {
  position: relative;
}
.percent{
  position: absolute;
  width: 100%;
  top: 28%;
  text-align: center;
}
.number{
  font-size: 3.5rem;
}
.word{
  font-size: 1.5rem;
  color: #000000;
  opacity: 0.5;
}
</style>



